.contact-page {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 30px;
  max-width: 900px;
  margin: auto;

  .form-card,
  .list-card {
    background: #fff;
    padding: 20px;
    border-radius: 12px;
    box-shadow: 0 3px 8px rgba(0, 0, 0, 0.1);
  }

  form {
    display: flex;
    flex-direction: column;
    gap: 12px;

    input,
    textarea {
      padding: 10px;
      border: 1px solid #ddd;
      border-radius: 8px;
      font-size: 1rem;
    }

    button {
      background: #007bff;
      color: white;
      padding: 10px;
      border: none;
      border-radius: 8px;
      cursor: pointer;
      transition: 0.3s;

      &:hover {
        background: #0056b3;
      }
    }

    .status {
      margin-top: 10px;
      font-weight: bold;
    }
  }

  table {
    width: 100%;
    border-collapse: collapse;

    th,
    td {
      border: 1px solid #ddd;
      padding: 10px;
      text-align: left;
    }

    th {
      background: #f0f0f0;
    }

    td button {
      margin-right: 6px;
      padding: 5px 10px;
      border: none;
      border-radius: 6px;
      cursor: pointer;

      &:first-child {
        background: #ffc107;
      }

