      position: relative;

      input,
      textarea {
        width: 100%;
        padding: 12px 12px 12px 0;
        border: none;
        border-bottom: 2px solid #ddd;
        border-radius: 4px 4px 0 0;
        outline: none;
        transition: border-color 0.3s;
        font-size: 1rem;
        background: transparent;
      }

      input:focus,
      textarea:focus {
        border-color: #11d822;
      }

      label {
        position: absolute;
        top: 12px;
        left: 0;
        color: #aaa;
        font-size: 0.9rem;
        pointer-events: none;
        transition: 0.3s;
      }

      input:focus + label,
      input:not(:placeholder-shown) + label,
      textarea:focus + label,
      textarea:not(:placeholder-shown) + label {
        top: -10px;
        font-size: 0.75rem;
        color: #11d822;
      }
    }

