      padding: 12px;
      border: none;
      border-radius: 8px;
      background: #11d822;
      color: #fff;
      font-size: 1rem;
      cursor: pointer;
      transition: 0.3s;

      &.loading {
        opacity: 0.7;
        cursor: not-allowed;
      }

      &:hover:not(.loading) {
        background: #0abf1f;
      }
    }

    .status {
      margin-top: 8px;
      font-weight: bold;

      &.success {
        color: #11d822;
      }

      &.error {
        color: #dc3545;
      }
    }
  }

