      padding: 6px 12px;
      border-radius: 6px;
      border: none;
      cursor: pointer;
      margin-right: 6px;
      font-weight: bold;
      transition: 0.3s;

      &.edit {
        background: #ffc107;
        &:hover { background: #e0a800; }
      }

