.auth-card {
  max-width: 360px;
  margin: 80px auto;
  padding: 30px;
  background: white;
  border-radius: 12px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.1);
  text-align: center;

  h2 {
    margin-bottom: 20px;
    color: #333;
  }

  input {
    display: block;
    width: 100%;
    padding: 12px;
    margin: 10px 0;
    border: 1px solid #ddd;
    border-radius: 8px;
  }

  button {
    width: 100%;
    padding: 12px;
    margin-top: 15px;
    background: linear-gradient(135deg, #007bff, #00c6ff);
    border: none;
    color: white;
    font-size: 1rem;
    border-radius: 8px;
    cursor: pointer;
    transition: 0.3s;

    &:hover {
      background: linear-gradient(135deg, #0056b3, #00a2cc);
    }
  }

  .switch {
    margin-top: 15px;
    font-size: 0.9rem;

    span {
      color: #0d1a28;
      cursor: pointer;
      font-weight: bold;
    }
  }
}
