.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 40px;
  background: linear-gradient(90deg, #5427f5, #3d3854);
  color: white;
  position: sticky;
  top: 0;
  z-index: 100;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.15);

  /* Logo */
  .logo {
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: transform 0.3s ease;

    .logo-img {
      width: 42px;
      height: auto;
      margin-right: 12px;
    }

    .logo-text {
      font-size: 1.8rem;
      font-weight: 700;
      letter-spacing: 1px;
      color: white;
    }

    &:hover {
      transform: scale(1.05);
    }
  }

  /* Navigation Links */
  .nav-links {
    display: flex;
    gap: 30px;

    .nav-item {
      cursor: pointer;
      position: relative;
      padding: 8px 4px;
      font-size: 1rem;
      font-weight: 500;
      transition: color 0.3s;

      &:hover {
        color: #e0ffe5;
      }

      &::after {
        content: "";
        position: absolute;
        left: 0;
        bottom: -4px;
        width: 0%;
        height: 3px;
        background: white;
        border-radius: 2px;
        transition: width 0.3s;
      }

      &:hover::after,
      &.active::after {
        width: 100%;
      }
    }
  }

  /* Hamburger Menu */
  .hamburger {
    display: none;
    flex-direction: column;
    justify-content: space-between;
    width: 30px;
    height: 22px;
    cursor: pointer;

    span {
      display: block;
      height: 3px;
      width: 100%;
      background: white;
      border-radius: 2px;
      transition: all 0.3s ease;
    }

    &.open span:nth-child(1) {
      transform: rotate(45deg) translate(5px, 5px);
    }
    &.open span:nth-child(2) {
      opacity: 0;
    }
    &.open span:nth-child(3) {
      transform: rotate(-45deg) translate(6px, -6px);
    }
  }

  /* Mobile Styles */
  @media (max-width: 768px) {
    .nav-links {
      position: absolute;
      top: 70px;
      right: 0;
      background: linear-gradient(180deg, #0ccf3d, #0a9f2a);
      flex-direction: column;
      width: 220px;
      border-radius: 10px 0 0 10px;
      padding: 20px;
      gap: 20px;
      transform: translateX(100%);
      transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
      opacity: 0;

      &.open {
        transform: translateX(0);
        opacity: 1;
      }

      .nav-item {
        padding: 12px 0;
        font-size: 1.1rem;
      }
    }

    .hamburger {
      display: flex;
    }
  }
}
