.home {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
  text-align: center;

  .hero {
    max-width: 600px;

    h1 {
      font-size: 2.5rem;
      margin-bottom: 15px;
    }

    p {
      margin-bottom: 20px;
      color: #555;
    }

    button {
      padding: 12px 24px;
      background: linear-gradient(135deg, #007bff, #00c6ff);
      border: none;
      border-radius: 8px;
      color: white;
      font-size: 1rem;
      cursor: pointer;
      transition: 0.3s;

      &:hover {
        background: linear-gradient(135deg, #0056b3, #00a2cc);
      }
    }
  }
}
